.profile__avatar-image {
    position: absolute;
    left: 0;
    top: 0;
    display: inline-block;
    margin: 0;
    object-fit: cover;
    width: 120px;
    height: 120px;
    border-radius: 50%;
}

@media all and (max-width: 650px) {
    .profile__avatar-image {
        margin: 0 auto;
    }
}